<template>
    <div class="goods_container">
        <h4 class="text-center">商品管理</h4>
        <!-- 头部 -->
        <el-row type="flex" justify="space-around" style="margin-bottom:10px">
            <!-- 搜索 -->
            <el-col :span="5">
                <el-input v-model="keyword" clearable placeholder="输入商品名称关键词" style="width:180px"
                    @keydown.enter.native="searchGoods"></el-input>
                <el-button type="primary" plain @click="searchGoods" :disabled="!keyword">搜索</el-button>
            </el-col>
            <!-- 分类 -->
            <el-col :span="5">
                <category-select placeholder="请选择分类" v-model="categoryId" style="display: inline-block; width: 120px;"></category-select>
                <el-button type="primary" plain :disabled="!categoryId" @click="showCategoryGoods">筛选</el-button>
            </el-col>
            <!-- 显示所有 -->
            <el-col :span="10">
                <el-button  plain @click="showAllGoods">显示所有</el-button>
            </el-col>
            <!-- 添加 -->
            <el-col :span="2">
                <el-button type="success" plain @click="addGoodsVisible = true">添加</el-button>
            </el-col>
        </el-row>
        <!-- 表格 -->
        <goods-table v-bind:goodslist="list" style="margin-bottom:10px"
            @cancelEmdit="requestGoodsData({},'已取消编辑，并刷新数据')"></goods-table>
        <!-- 页 -->
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
        <!-- 添加 -->
        <add-goods v-bind:dialogVisible="addGoodsVisible" @close="addGoodsVisible = false"
            @addSuccess="onAddSuccess"></add-goods>
    </div>
</template>

<script>
import CategorySelect from '../category/CategorySelect.vue';
import GoodsTable from '../goods/GoodsTable.vue';
import AddGoods from '../goods/AddGoods.vue';

export default {
    components: { GoodsTable, AddGoods, CategorySelect },
    name: 'MyGoods',
    data() {
        return {
            addGoodsVisible: false,//是否显示添加页面
            currentPage: 1,//当前页码
            total: 0,//列表数量总数
            pageSize: 5,//当前页码的数量
            keyword: null,//搜索内容
            categoryId: null,//分类显示的分类ID
            list: [],//列表数据
        }
    },
    methods: {
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.requestGoodsData({ currentPage: val })
        },
        searchGoods() {
            let keyword = this.keyword
            this.requestGoodsData({ keyword })
        },
        showCategoryGoods() {
            let categoryId = this.categoryId
            this.requestGoodsData({ categoryId })
        },
        showAllGoods(){
            //将分类显示的分类ID = null,清空搜索内容
            this.categoryId = null
            this.keyword = ''
            this.requestGoodsData({})
        },
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '成功加载数据') {
            console.log(currentPage, pageSize, keyword, categoryId)
            this.$https.get('/product/page', {
            // this.$https.get('/data/test.json', {
                params: {
                    currentPage,
                    pageSize,
                    keyword,
                    categoryId
                }
            })
                .then(function (response) {
                    console.log(response);
                    //状态200时
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: successMsg,
                            type: 'success'
                        })
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                        console.log(this.list, this.total);
                    } else {
                        this.$message.error(response.data.msg)
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('出错了')
                }.bind(this))
        },
        onAddSuccess(msg) {
            this.requestGoodsData({}, msg)
        }

    },
    mounted: function () {
        //发送请求，请求商品第一页
        this.requestGoodsData({})
    }
}
</script>

<style lang="less" scoped></style>